/* Empty Thumbnail */

.emptyThumbnailContainer{
    margin: 0;
}

.emptyThumbnail_content{
    font-weight: 400;
}

/* Thumbnail */

.thumbnailContainer{
    background-color: black;
     border-radius: 4px;
     overflow: hidden;
     width: 350px;
     height: 450px;
     border-radius: 2px;
     cursor: pointer;
}

.thumbnailBody{
    overflow: hidden;
}

.thumbnailImg{
    height: 250px;
    width: 100%;
} 

.thumbnailContentContainer{
 padding: 10px ;
}

.thumbnailTitle{
    font-weight: 450;
    white-space:pre-wrap; 
    word-break: break-all;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    width: 274px; 
    overflow: hidden;
}

.thumbnailSubTitle {
    font-size: 200;
    font-size: 13px;
}

.thumbnailContent{
    font-size: 200;
    font-size: 13px;
    margin: 0;
    white-space:pre-wrap; 
   overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
}


.thumbnailRatingContent{
     margin-top: 20px;
}

.thumbnailIcon{
        width: fit-content;
        margin-top:-4px ;
}