
.mainLayoutWrapper{
    height: 100vh;
    display: grid;
    grid-template-rows: 60px 1fr 30px;
}

/* Header */
.headerContainer{
    margin: 0;
    padding: 10px;
}

.header_Title{
    margin: 0;
    font-weight: 400;
}


/* Content */
.contentContainer{
    overflow-y: auto;
    padding: 20px;
}

.content{
    margin: 10px 0px;
    border-radius:3px;
    border: 1px dashed black;
    gap:10px;
}

/* Footer */
.footerContainer{
  margin: 0;
  padding: 0;
}

.footer_Content{
    
    font-weight: 400;
    color: #b6b6b6;
}