.buttonContainer{
    border-radius: 30px;
    outline: none;
    border: none;
    padding: 20px;
    font-size: 300;
    cursor: pointer;
}


.buttonContainer:disabled{
    opacity: .6;
    background-color: #a6a6a6 ;
    color:rgba(0, 0, 0, 0.6);
    cursor: auto;
    user-select: none;
}